.cac-chinh-sach {
	margin       : 60px 0 80px;
	padding-right: 40px;

	.main-title {
		font-size    : 34px;
		color        : #2B2B2B;
		font-weight  : 700;
		margin-bottom: 40px;
	}

	.main-content {
		p {
			font-size    : 18px;
			color        : #4D4D4D;
			margin-bottom: 35px;

			&.bold {
				color      : #2B2B2B;
				font-weight: 700;
			}
		}

		figure {
			margin: 30px 0;

			img {
				width: 100%;
			}
		}
	}

	.detail-title {
		font-size    : 18px;
		font-weight  : 700;
		color        : #3E4095;
		margin-bottom: 15px;
	}

	.detail-content {
		p {
			font-size    : 18px;
			color        : #4D4D4D;
			margin-bottom: 25px;
		}
	}

	figure {
		margin: 50px 0;

		img {
			width: 100%;
		}
	}

	@media (max-width: 1024.98px) {
		margin       : 30px 0;
		padding-right: 15px;

		.main-title {
			margin-bottom: 15px;
		}

		.main-content {
			p {
				margin-bottom: 15px;
			}

			figure {
				margin: 15px 0;
			}
		}

		.detail-content {
			p {
				margin-bottom: 15px;
			}
		}

		figure {
			margin: 15px 0;
		}
	}

	@media (max-width: 767.98px) {
		margin: 15px 0;

		.main-title {
			font-size: 24px;
		}

		.main-content {
			p {
				font-size: 16px;
			}
		}

		.detail-title {
			font-size: 16px;
		}

		.detail-content {
			p {
				font-size: 16px;
			}
		}
	}
}