.service-list {
	margin: 75px 0;

	h4 {
		font-size    : 18px;
		font-weight  : 700;
		color        : #2B2B2B;
		margin-bottom: 20px;
	}

	.list-img {
		display  : flex;
		flex-wrap: wrap;
		margin   : 0 -7.5px;

		.item {
			flex         : 0 0 50%;
			max-width    : 50%;
			padding      : 0 7.5px;
			margin-bottom: 15px;
		}
	}

	.list-item-service {
		padding-left: 10px;

		.item {
			display      : flex;
			flex-wrap    : wrap;
			margin-bottom: 20px;

			h5 {
				flex     : 0 0 40%;
				max-width: 40%;

				font-size  : 18px;
				font-weight: 700;
				color      : #2B2B2B;
			}

			ul {
				flex     : 0 0 60%;
				max-width: 60%;

				li {
					font-size: 18px;
					color    : #707070;
				}
			}
		}
	}


	@media (max-width: 1024.98px) {
		margin: 15px 0;
	}

	@media (max-width: 767.98px) {
		h4 {
			font-size: 16px;
		}

		.list-item-service {
			padding-left: 0px;

			.item {
				margin-bottom: 15px;

				h5 {
					flex         : 0 0 100%;
					max-width    : 100%;
					margin-bottom: 15px;
					font-size    : 16px;
				}

				ul {
					flex     : 0 0 100%;
					max-width: 100%;

					li {
						font-size: 16px;
					}
				}
			}
		}
	}
}